(function() {
  'use strict';

  angular
    .module('web')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('web')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('web')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('TestAnswersController', TestAnswersController);

    /** @ngInject */
    function TestAnswersController($http, CommonInfo, _) {
        var vm = this;

        vm.subjectWise = {};

        activate();

        function activate() {
            vm.exam = CommonInfo.getInfo('exam');
            vm.user = CommonInfo.getInfo('user');
            getUserAnswers();
        }

        function getUserAnswers() {
            var data = {
                userId: vm.user.id,
                testId: vm.exam.id
            };
            $http.post(CommonInfo.getAppUrl() + '/api/exam/userAnswers', data).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    vm.userAnswers = response.data.userAnswers;
                    if(vm.userAnswers.score){
                        getTestUsers();
                    }
                    vm.userAnswers.totalCorrect = 0;
                    vm.userAnswers.totalIncorrect = 0;
                    vm.userAnswers.totalUnanswered = 0;
                    if (vm.userAnswers && vm.userAnswers.questions && vm.userAnswers.questions.length > 0) {
                        if (vm.userAnswers.userInfo && vm.userAnswers.userInfo.length > 0) {
                            _.forEach(vm.userAnswers.questions, function(value) {
                                if (value.type != 'Passage') {
                                    vm.subjectWise[value.subjectId] = vm.subjectWise[value.subjectId] || {total:0, correct:0, unanswered:0, incorrect:0};
                                    vm.subjectWise[value.subjectId].name = value.subjectName;
                                    vm.subjectWise[value.subjectId].total++;

                                    var userAnswer = _.find(vm.userAnswers.userInfo, { 'questionId': value.id });
                                    value.userAnsKey = userAnswer ? userAnswer.answer : null;
                                    if(value.userAnsKey == value.correctAnswer){
                                        vm.userAnswers.totalCorrect++;
                                        vm.subjectWise[value.subjectId].correct++;
                                        value.result = 'correct';
                                    }
                                    else if(!value.userAnsKey){
                                        vm.userAnswers.totalUnanswered++;
                                        vm.subjectWise[value.subjectId].unanswered++;
                                        value.result = 'unanswered';
                                    }
                                    else{
                                        vm.subjectWise[value.subjectId].incorrect++;
                                        vm.userAnswers.totalIncorrect++;
                                        value.result = 'incorrect';
                                    }
                                    if (value.userAnsKey)
                                        value.userAnswer = _.find(value.answers, { 'ansKey': value.userAnsKey }).answerText;
                                    else
                                        value.userAnswer = "Not Answered"
                                    value.correctAnsKey = value.correctAnswer;
                                    if(value.correctAnswer)
                                        value.correctAnswer = _.find(value.answers, { 'ansKey': value.correctAnswer }).answerText;
                                }
                            });
                            vm.questions = [];
                            _.forEach(vm.userAnswers.questions, function(value){
                                if(value.type == 'MCQ'){
                                    vm.questions.push(value);
                                } else if(value.type == 'Passage'){
                                    var childQuestions = _.filter(vm.userAnswers.questions, { 'parentQuestionId': value.id});
                                    childQuestions[0].passage = value.question;
                                    vm.questions = vm.questions.concat(childQuestions);
                                }
                            });
                            console.log(vm.subjectWise)
                        }
                    }
                }
            }, function(response) {});
        }

        function getTestUsers(){
            $http.post(CommonInfo.getAppUrl() + '/api/test/users', { 'testId': vm.exam.id, withEvaluatedStatus: true }).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    vm.testUsers = response.data.testUsers;
                }
            }, function(response) {});
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('TestController', TestController)
        .filter('htmlToPlaintext', htmlToPlaintext);

    function htmlToPlaintext() {
        return function(text) {
            return angular.element(text).text();
        }
    }

    /** @ngInject */
    function TestController($http, CommonInfo, $state, growl, $uibModal, Upload, _, $window) {
        var vm = this;
        var selectedQuestionGrid = [];

        vm.objMode;
        vm.selectedTab = 0;
        vm.isQuestionSelected = false;
        vm.formats = ['dd-MMMM-yyyy', 'yyyy/MM/dd', 'dd.MM.yyyy', 'shortDate'];
        vm.format = 'dd-MMMM-yyyy';
        vm.toolBar = [
            ['h1', 'h2', 'h3', 'bold', 'italics', 'underline'],
            ['ol', 'ul'],
            ['justifyLeft', 'justifyCenter', 'justifyRight', 'justifyFull'],
            ['html', 'insertImage', 'insertLink', 'insertVideo']
        ];
        vm.questionType = ['MCQ', "Passage"];
        vm.keyCode = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't'];

        vm.getAllTests = getAllTests;
        vm.getTestById = getTestById;
        vm.editTest = editTest;
        vm.updateTest = updateTest
        vm.deleteTest = deleteTest;
        vm.evaluation = evaluation;
        vm.evaluationAll = evaluationAll;
        vm.getAllquestionPapers = getAllquestionPapers;
        vm.editQuestionPaper = editQuestionPaper;
        vm.addSubject = addSubject;
        vm.deleteQuestionPaper = deleteQuestionPaper;
        vm.getAllQuestions = getAllQuestions;
        vm.questionSelection = questionSelection;
        vm.getQuestionById = getQuestionById;
        vm.editQuestion = editQuestion;
        vm.updateQuestion = updateQuestion;
        vm.deleteQuestion = deleteQuestion;
        vm.addToPaper = addToPaper;
        vm.importQuestion = importQuestion;
        vm.getTestUsers = getTestUsers;
        vm.showTestPreview = showTestPreview;
        activate();

        function activate() {
            getAllTests();
            getAllSubjects();
        }

        //////// Test related ///////////////////////////////////////////////////////////
        function getAllTests() {
            resetQuestionSelection();
            $http.get(CommonInfo.getAppUrl() + '/api/test/all').then(function(response) {
                if (response && response.data) {
                    vm.tests = response.data.tests;
                }
            }, function(response) {});
        }

        function getTestById(testId) {
            if (testId) {
                $http.post(CommonInfo.getAppUrl() + '/api/test/byId', { 'testId': testId }).then(function(response) {
                    if (response && response.data && response.data.test) {
                        vm.test = response.data.test;
                        editTest('edit', vm.test);
                    }
                }, function(response) {});
            }
        }

        function editTest(mode, test) {
            vm.objMode = mode;
            getAllquestionPapers();
            if (mode == 'edit') {
                $state.go('main.test.editTest');
                vm.test = test;
                vm.test.startDate = vm.test.startDate ? new Date(vm.test.startDate) : "";
                vm.test.endDate = vm.test.endDate ? new Date(vm.test.endDate) : "";
            } else if (mode == 'insert') {
                vm.test = {};
                $state.go('main.test.createTest');
            }
        }

        function updateTest() {
            if (vm.test) {
                $http.post(CommonInfo.getAppUrl() + '/api/test', vm.test).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Test added successfully');
                        $state.go('main.test.home');
                        getAllTests();
                    }
                }, function(response) {});
            }
        }

        function deleteTest(testId) {
            if (confirm('Are you sure, you want to delete test')) {
                $http.post(CommonInfo.getAppUrl() + '/api/test/delete', { 'testId': testId }).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Test deleted successfully');
                        getAllTests();
                    }
                }, function(response) {});
            }
        }

        function evaluation(testId) {
            $http.post(CommonInfo.getAppUrl() + '/api/exam/instantEvaluation', { 'testId': testId }).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    growl.success('Test evaluation successfully');
                    //getAllTests();
                }
            }, function(response) {});
        }

        function evaluationAll(testId){
            $http.post(CommonInfo.getAppUrl() + '/api/exam/evaluation', { 'testId': testId, 'isForced': true }).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    growl.success('Test evaluation successfully');
                    //getAllTests();
                }
            }, function(response) {});
        }

        ////////// Question Paper related ////////////////////////////////////////////////////////
        function getAllquestionPapers(holdSelection) {
            if (!holdSelection)
                resetQuestionSelection();
            $http.get(CommonInfo.getAppUrl() + '/api/questionPaper/all').then(function(response) {
                if (response && response.data) {
                    vm.questionPapers = response.data.questionPapers;
                }
            }, function(response) {});
        }

        function editQuestionPaper(mode, questionPaper) {
            var modalInstance = $uibModal.open({
                animation: true,
                templateUrl: 'editQuestionPaper.html',
                controller: function($scope, item) {
                    $scope.quesPaper = item;
                    $scope.ok = function() {
                        if ($scope.quesPaper.name) {
                            $http.post(CommonInfo.getAppUrl() + '/api/questionPaper', $scope.quesPaper).then(function(response) {
                                if (response && response.data && !response.data.Error) {
                                    growl.success('Question paper added successfully');
                                }
                            }, function(response) {});
                        }
                    };
                },
                resolve: {
                    item: function() {
                        return questionPaper || {};
                    }
                }
            });
        }

        function getAllSubjects() {
            $http.get(CommonInfo.getAppUrl() + '/api/subject/all').then(function(response) {
                if (response && response.data) {
                    vm.subjectList = response.data.subjects;
                }
            }, function(response) {});
        }

        function addSubject(){
            var modalInstance = $uibModal.open({
                animation: true,
                templateUrl: 'addSubjects.html',
                controller: function($scope, item) {
                    $scope.subjects = item;
                    $scope.ok = function() {
                        if ($scope.subject.name) {
                            $http.post(CommonInfo.getAppUrl() + '/api/subject', $scope.subject).then(function(response) {
                                if (response && response.data && !response.data.Error) {
                                    growl.success('Subject added successfully');
                                    getAllSubjects();
                                }
                            }, function(response) {});
                        }
                    };
                },
                resolve: {
                    item: function() {
                        return vm.subjectList || [];
                    }
                }
            });
        }

        function deleteQuestionPaper(quesPaperId) {
            if (confirm('Are you sure, you want to delete question paper')) {
                $http.post(CommonInfo.getAppUrl() + '/api/questionPaper/delete', { 'questionPaperId': quesPaperId }).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Test deleted successfully');
                        getAllquestionPapers();
                    }
                }, function(response) {});
            }
        }

        //////////Question related ////////////////////////////////////////////////////
        function getAllQuestions() {
            $http.get(CommonInfo.getAppUrl() + '/api/question/all').then(function(response) {
                if (response && response.data) {
                    vm.questions = response.data.questions;
                }
            }, function(response) {});
        }

        function questionSelection() {
            selectedQuestionGrid = _.map(_.filter(vm.questions, { 'isSelected': true }), 'id');
            if (selectedQuestionGrid && selectedQuestionGrid.length > 0) {
                vm.isQuestionSelected = true;
                if (!vm.questionPapers)
                    getAllquestionPapers(true);
            } else {
                vm.isQuestionSelected = false;
            }
        }

        function getQuestionById(questionId) {
            if (questionId) {
                $http.post(CommonInfo.getAppUrl() + '/api/question/byId', { 'questionId': questionId }).then(function(response) {
                    if (response && response.data && response.data.question) {
                        vm.question = response.data.question;
                        editQuestion('edit', vm.question);
                    }
                }, function(response) {});
            }
        }

        function editQuestion(mode, question) {
            vm.objMode = mode;
            vm.question = question || {};
            vm.question.correctAnswer = vm.question.correctAnswer || '';
            vm.question.answers = vm.question.answers || [{ answerText: '', isDeleted: 0 }];
            vm.question.childQuestions = vm.question.childQuestions || '';
            vm.question.childQuestions = vm.question.childQuestions || [{ question: '', isDeleted: 0, answers: [{ answerText: '' }] }];
            if (mode == 'edit') {
                $state.go('main.test.editQuestion');
            } else if (mode == 'insert') {
                $state.go('main.test.createQuestion');
            }
        }

        function updateQuestion(addNew) {
            if (vm.question) {
                $http.post(CommonInfo.getAppUrl() + '/api/question', vm.question).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Question added successfully');
                        if(!addNew){
                            $state.go('main.test.home');
                            getAllQuestions();
                        } else {
                            vm.question = {
                                answers: [{ answerText: '', isDeleted: 0 }],
                                childQuestions: [{ question: '', isDeleted: 0, answers: [{ answerText: '' }] }]
                            };
                        }
                    }
                }, function(response) {});
            }
        }

        function deleteQuestion(questionId) {
            if (confirm('Are you sure, you want to delete question')) {
                $http.post(CommonInfo.getAppUrl() + '/api/question/delete', { 'questionId': questionId }).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Question deleted successfully');
                        getAllQuestions();
                    }
                }, function(response) {});
            }
        }

        function addToPaper(paperId) {
            if (paperId && selectedQuestionGrid && selectedQuestionGrid.length > 0) {
                var data = {
                    questionPaperId: paperId,
                    questionIds: selectedQuestionGrid
                };
                $http.post(CommonInfo.getAppUrl() + '/api/question/addToPaper', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Question added successfully');
                        resetQuestionSelection();
                    }
                }, function(response) {});
            }
        }

        function getTestUsers(test) {
            $http.post(CommonInfo.getAppUrl() + '/api/test/users', { 'testId': test.id }).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    vm.testUsers = response.data.testUsers;
                    $state.go('main.test.studentList');
                }
            }, function(response) {});
        }

        function showTestPreview(test) {
            test.isPreview = true
            CommonInfo.setInfo('exam', test);
            $window.open($state.href('main.exam', {isPreview: true}),'_blank');
        }

        function importQuestion(file) {
            if (vm.question.file) {
                Upload.upload({
                    url: CommonInfo.getAppUrl() + '/api/question/import',
                    data: {
                        file: vm.question.file,
                    }
                }).then(function(resp) {
                    console.log('Success ' + resp.config.data.file.name + 'uploaded. Response: ' + resp.data);
                }, function(resp) {
                    console.log('Error status: ' + resp.status);
                }, function(evt) {
                    var progressPercentage = parseInt(100.0 * evt.loaded / evt.total);
                    console.log('progress: ' + progressPercentage + '% ' + evt.config.data.file.name);
                });
            }
        }

        function resetQuestionSelection() {
            _.forEach(vm.questions, function(value) {
                value.isSelected = false;
            });
            questionSelection();
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('ExamListController', ExamListController);

    /** @ngInject */
    function ExamListController($http, CommonInfo, $state, _) {
        var vm = this;

        vm.startExam = startExam;
        vm.showAnswers = showAnswers;

        activate();

        function activate() {
            CommonInfo.setInfo('exam', '');
            vm.user = CommonInfo.getInfo('user');
            getAllExams();
        }

        function getAllExams() {
            $http.post(CommonInfo.getAppUrl() + '/api/exam/all', { 'userId': vm.user.id }).then(function(response) {
                if (response && response.data) {
                    _.forEach(response.data.tests, function(value){
                        value.durationInHrs = moment.duration(value.duration, 'seconds').format("HH:mm:ss")
                    });
                    vm.exams = response.data.tests;
                }
            }, function(response) {});
        }

        function startExam(test) {
            CommonInfo.setInfo('exam', test);
            $state.go('main.exam');
        }

        function showAnswers(test) {
            CommonInfo.setInfo('exam', test);
            $state.go('main.examAnswers');
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('ExamController', ExamController)
        .directive('instructionBoard', instructionBoard)
        .directive('questionArea', questionArea)
        .directive('navigationArea', navigationArea);


    /** @ngInject */
    function instructionBoard(resize, $window) {
        return {
            restrict: 'A',

            link: function(scope, elem, attrs) {

                resize(scope).call(function() {
                    elem.css('height', $window.innerHeight - 120 + 'px');
                });
            }
        };
    }

    /** @ngInject */
    function questionArea(resize, $window) {
        return {
            restrict: 'A',

            link: function(scope, elem, attrs) {

                resize(scope).call(function() {
                    elem.css('height', $window.innerHeight - 131 + 'px');
                });
            }
        };
    }

    function navigationArea(resize, $window) {
        return {
            restrict: 'A',

            link: function(scope, elem, attrs) {

                resize(scope).call(function() {
                    elem.css('height', $window.innerHeight - 220 + 'px');
                });
            }
        };
    }

    /** @ngInject */
    function ExamController($http, CommonInfo, $state, $scope, growl, $interval, $window) {
        var vm = this;
        var userTime;
        var submitAttempt = 0;
        var userInfoLocalStorage;
        var localSubmit, serverSubmit;

        vm.isExamStarted = false;
        vm.isExamEnded = false;
        vm.userCurrentQuestion = [];
        vm.timer = 0;

        vm.startExam = startExam;
        vm.showQuestion = showQuestion;
        vm.submitExam = submitExam;
        vm.saveLocal = saveLocal;

        activate();

        function activate() {
            vm.exam = CommonInfo.getInfo('exam');
            vm.isPreview = vm.exam.isPreview ? true : false;
            vm.user = CommonInfo.getInfo('user');
            if (vm.exam && vm.user) {
                vm.timer = vm.exam.duration;
                getExamQuestions();
            }
        }

        $scope.$on('timer-tick', function(event, args) {
            userTime = args.millis;
        });

        $scope.$on('timer-stopped', function(event, data) {
            vm.isExamEnded = true;
            alert('Your exam time is over, press ok to submit exam');
            submitExam(true);
        });

        function getExamQuestions() {
            $http.post(CommonInfo.getAppUrl() + '/api/exam/byId', { testId: vm.exam.id }).then(function(response) {
                if (response && response.data) {
                    vm.exam.questions = response.data.questions;
                    userInfoLocalStorage = CommonInfo.getInfo(vm.user.id + '_' + vm.exam.id);
                    getUserTestInfo();
                }
            }, function(response) {});
        }

        function getUserTestInfo() {
            var data = {
                userId: vm.user.id,
                testId: vm.exam.id
            };
            if (!vm.isPreview) {
                $http.post(CommonInfo.getAppUrl() + '/api/exam/userInfo', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        if (compareTimestamp()) {
                            vm.exam.userInfo = userInfoLocalStorage;
                        } else {
                            vm.exam.userInfo = response.data.userTestInfo;
                        }
                        if (vm.exam && vm.exam.userInfo && vm.exam.userInfo.status == 'pending' && vm.exam.userInfo.timeRemaining == 0) {
                            alert('Your exam time is over, press ok to submit exam')
                            submitExam(true);
                        } else {
                            vm.timer = vm.exam.userInfo.timeRemaining || vm.timer;
                            _.forEach(vm.exam.questions, function(value, key) {
                                vm.userCurrentQuestion[key] = _.find(vm.exam.userInfo.answers, { 'questionId': value.id }) || {
                                    questionId: value.id,
                                    answer: '',
                                    isMarked: false
                                };
                            });
                        }
                    } else {
                        growl.info('Some error occured, try after some time');
                        $state.go('main.examsList');
                    }
                }, function(response) {
                    growl.info('Some error occured, try after some time');
                    $state.go('main.examsList');
                });
            } else {
                _.forEach(vm.exam.questions, function(value, key) {
                    vm.userCurrentQuestion[key] = {
                        questionId: value.id,
                        answer: '',
                        isMarked: false
                    };
                });
            }
        }

        function compareTimestamp() {
            if (userInfoLocalStorage && userInfoLocalStorage.timestamp) {
                if (vm.exam.userInfo && vm.exam.userInfo.timestamp) {
                    if (moment(vm.exam.userInfo.timestamp).isBefore(userInfoLocalStorage.timestamp))
                        return true;
                    else
                        return false;
                } else {
                    return true;
                }
            } else {
                return false;
            }
        }

        function startExam() {
            if (vm.exam && vm.exam.questions && vm.exam.questions.length > 0) {
                vm.isExamStarted = true;
                showQuestion(0);
                localSubmit = $interval(function() { saveLocal() }, 10000);
                serverSubmit = $interval(function() { submitExam(false, 'pending') }, 60000);
                $scope.$on('$stateChangeStart',
                    function(event, toState, toParams, fromState, fromParams) {
                        if (vm.isExamStarted && !vm.isExamEnded)
                            event.preventDefault();
                    });
                stopNavigation();
            }
        }

        function stopNavigation() {
            // var myEvent = $window.attachEvent || $window.addEventListener,
            //     chkevent = $window.attachEvent ? 'onbeforeunload' : 'beforeunload';

            // myEvent(chkevent, function(e) {
            //     submitExam(false, 'pending');
            //     var confirmationMessage = ' ';
            //     (e || $window.event).returnValue = "Are you sure that you'd like to close the browser?";
            //     return confirmationMessage;
            // });

            // $scope.$on('$locationChangeStart', function(event) {
            //     submitExam(false, 'pending');
            //     var answer = confirm("Are you sure you want to leave this page?")
            //     if (!answer) {
            //         event.preventDefault();
            //     }
            // });
        }

        function showQuestion(questionNo) {
            vm.currentIndex = questionNo;
            vm.currentQuestionIndex = questionNo + 1;
            vm.currentQuestion = vm.exam.questions[questionNo];
            saveLocal();
        }

        function saveLocal() {
            //submitExam(false, 'pending');
            console.log(123)
            var data = {
                userId: vm.user.id,
                testId: vm.exam.id,
                timeRemaining: userTime / 1000,
                answers: vm.userCurrentQuestion,
                status: 'pending',
                timestamp: moment()
            };
            CommonInfo.setInfo(vm.user.id + '_' + vm.exam.id, data);
        }

        function submitExam(isForced, status) {
            var confirmation = false;
            status = status || 'completed';
            var data = {
                userId: vm.user.id,
                testId: vm.exam.id,
                timeRemaining: userTime / 1000,
                answers: vm.userCurrentQuestion,
                status: status,
                timestamp: moment()
            };
            if (!vm.isPreview) {
                if (isForced || status == 'pending' || confirm('Are you sure, you want to submit(final) your answers')) {
                    $http.post(CommonInfo.getAppUrl() + '/api/exam/submit', data).then(function(response) {
                        if (response && response.data) {
                            if (status == 'completed') {
                                vm.isExamEnded = true;
                                $interval.cancel(localSubmit);
                                $interval.cancel(serverSubmit);
                                localSubmit = serverSubmit = undefined;
                                growl.success('Your answers saved successfully');
                                $state.go('main.examsList');
                            } else {
                                if (status != 'pending') {
                                    if (submitAttempt <= 3) {
                                        submitAttempt++;
                                        submitExam(true);
                                    } else {
                                        growl.info('Unable to submit due to some server error, please try after some time');
                                        $state.go('main.examsList');
                                    }
                                }
                            }
                        }
                    }, function(response) {
                        if(status != 'pending') {
                            if (submitAttempt <= 3) {
                                submitAttempt++;
                                submitExam(true);
                            } else {
                                growl.info('Unable to submit due to some server error, please try after some time');
                                $state.go('main.examsList');
                            }
                        }
                    });
                }
            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('MainController', MainController)
        .filter('truncate', truncate);

    /** @ngInject */
    function truncate(_) {
        return function(text) {
            return _.truncate(text, { 'length': 213 });
        }
    }

    /** @ngInject */
    function MainController($http, CommonInfo, Upload, $state, credentials, $uibModal, _, growl, $scope) {
        var vm = this;

        vm.userInfo; // hold user info[both]
        vm.isCollapsed = true; // for the drop down main menu[both]
        vm.lesson = {}; // current lesson on (my, edit, add) lesson page[both]
        vm.config = {}; // user configuration for showing tabs[both]
        vm.courses = []; // list of (Library, my, all) courses[both]
        vm.units = []; // list of units, lessons to show on my lessons page[student]
        //for admin side only
        vm.categoryPopup = false;
        vm.categories = []; // list of categories in create/edit course page
        vm.lessons = []; // list of all lessons in admin role
        vm.lessonComments = []; //list of comments by lesson id
        vm.users = []; // list of (instructor, student) users in admin role
        vm.user = {};
        vm.course = { // object used while (create/edit) course
            units: [],
            instructors: []
        };
        vm.objMode; // hold the current mode for entity(edit/insert)
        vm.dateOptions = {
            formatYear: 'yy',
            startingDay: 1
        };
        vm.formats = ['dd-MMMM-yyyy', 'yyyy/MM/dd', 'dd.MM.yyyy', 'shortDate'];
        vm.format = 'dd-MMMM-yyyy';
        vm.toolBar = [
            ['h1', 'h2', 'h3', 'bold', 'italics', 'underline'],
            ['ol', 'ul'],
            ['justifyLeft', 'justifyCenter', 'justifyRight', 'justifyFull'],
            ['html', 'insertImage', 'insertLink', 'insertVideo']
        ];

        vm.getAllCourses = getAllCourses;
        vm.subscribeCourse = subscribeCourse;
        vm.getMyCourses = getMyCourses;
        vm.selectCourse = selectCourse;
        vm.searchCourse = searchCourse;
        vm.selectLesson = selectLesson;
        vm.commentOnLesson = commentOnLesson;
        vm.updateProfile = updateProfile;
        vm.showCourseDemo = showCourseDemo;
        vm.showCourseInfo = showCourseInfo;
        vm.downloadFile = downloadFile;
        vm.signout = signout;
        //for admin side
        vm.addCategory = addCategory;
        vm.getCourseById = getCourseById;
        vm.editCourse = editCourse;
        vm.addUnitToCourse = addUnitToCourse;
        vm.addUserToCourse = addUserToCourse;
        vm.updateCourse = updateCourse;
        vm.deleteCourse = deleteCourse;
        vm.getAllLessons = getAllLessons;
        vm.getLessonById = getLessonById;
        vm.editLesson = editLesson;
        vm.changeCourse = changeCourse;
        vm.addFilesToLesson = addFilesToLesson;
        vm.updateLesson = updateLesson;
        vm.deleteLesson = deleteLesson;
        vm.getLessonComments = getLessonComments;
        vm.getUsers = getUsers;
        vm.approveUser = approveUser;
        vm.getUserById = getUserById;
        vm.editUser = editUser;


        activate();

        function activate() {
            console.log(moment())
            vm.config = credentials.getCredentials();
            vm.userInfo = CommonInfo.getInfo('user');
            if (vm.userInfo && vm.userInfo.profileType == 'student') {
                if ($state.is('main.libary')) {
                    getAllCourses();
                } else if ($state.is('main.myCourses')) {
                    getMyCourses();
                } else if ($state.is('main.myLessons')) {
                    var course = CommonInfo.getInfo('course');
                    getUnits(course.id);
                } else if ($state.is('main.profile')) {
                    var user = angular.copy(vm.userInfo);
                    if (user && user.id)
                        editUser('update', user);
                }
            } else if (vm.userInfo && vm.userInfo.profileType == 'admin') {
                if ($state.is('main.courses')) {
                    getAllCourses();
                    showCategoryModal();
                } else if ($state.is('main.lessons')) {
                    getAllLessons();
                } else if ($state.is('main.instructors')) {
                    getUsers('instructor');
                } else if ($state.is('main.students')) {
                    getUsers('student');
                } else if ($state.is('main.createCourse')) {
                    editCourse('insert');
                } else if ($state.is('main.editCourse')) {
                    var course = CommonInfo.getInfo('course');
                    if (course && course.id)
                        editCourse('edit', course);
                } else if ($state.is('main.createLesson')) {
                    editLesson('insert');
                } else if ($state.is('main.editLesson')) {
                    var lesson = CommonInfo.getInfo('lesson');
                    if (lesson && lesson.id)
                        editLesson('edit', lesson);
                } else if ($state.is('main.createUser')) {
                    editUser('insert');
                } else if ($state.is('main.editUser')) {
                    var user = CommonInfo.getInfo('editUser');
                    if (user && user.id)
                        editUser('edit', user);
                } else if ($state.is('main.profile')) {
                    var user = angular.copy(vm.userInfo);
                    if (user && user.id)
                        editUser('update', user);
                } else if ($state.is('main.comments')) {
                    var lesson = CommonInfo.getInfo('lesson');
                    if (lesson && lesson.id)
                        showLessonComments(lesson.id);
                }
            }
        }

        function getAllCourses() {
            $http.get(CommonInfo.getAppUrl() + '/api/course/all').then(function(response) {
                if (response && response.data) {
                    vm.courses = response.data.courses;
                }
            }, function(response) {});
        }

        function searchCourse() {
            if (vm.searchText) {
                var data = {
                    name: vm.searchText
                }
                $http.post(CommonInfo.getAppUrl() + '/api/course/search', data).then(function(response) {
                    if (response && response.data) {
                        $state.go('main.libary');
                        vm.courses = response.data.courses;
                    }
                }, function(response) {});
            }
        }

        function subscribeCourse(course) {
            if (course) {
                var data = {
                    phone: vm.userInfo.phone,
                    fullName: vm.userInfo.fullName,
                    email: vm.userInfo.email,
                    purpose: 'Payment stud: ' + vm.userInfo.id + 'cour: ' + course.id,
                    amt: course.subscriptionFee,
                    userId: vm.userInfo.id,
                    courseId: course.id
                };
                $http.post(CommonInfo.getAppUrl() + '/api/course/subscribe', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        if (response.data.url) {
                            window.open(response.data.url + '?embed=form');
                        } else if (response.data.code) {
                            growl.success('Course subscribed successfully');
                        }
                    }
                }, function(response) {});
            }
        }

        function getMyCourses() {
            var data = {
                userId: vm.userInfo.id
            };
            $http.post(CommonInfo.getAppUrl() + '/api/course/subscribed', data).then(function(response) {
                if (response && response.data) {
                    vm.courses = response.data.courses;
                }
            }, function(response) {});
        }

        function selectCourse(course) {
            if (course) {
                CommonInfo.setInfo('course', course);
                getUnits(course.id, true);
            }
        }

        function getUnits(courseId, navigateToPage) {
            if (courseId) {
                var data = {
                    courseId: courseId
                };
                $http.post(CommonInfo.getAppUrl() + '/api/unit/byCourse', data).then(function(response) {
                    if (response && response.data && response.data.units.length > 0) {
                        vm.units = response.data.units;
                        selectLesson(vm.units[0].lessons[0]);
                        if (navigateToPage)
                            $state.go('main.myLessons');
                    } else {
                        growl.info('No units present in this course');
                    }
                }, function(response) {

                });
            }
        }

        function selectLesson(lesson) {
            vm.lesson = lesson
            vm.lesson.comments = vm.lesson.comments || [];
            if (lesson.video) {
                vm.lesson.options = {
                    file: lesson.video,
                    image: lesson.poster,
                    type: 'hls',
                    androidhls: 'true',
                    width: '100%',
                    player: 'html5',
                    aspectratio: '16:9'
                }
            } else {
                vm.lesson.options = null;
            }
        }

        function getAllLessons() {
            $http.get(CommonInfo.getAppUrl() + '/api/lesson/all').then(function(response) {
                if (response && response.data) {
                    vm.lessons = response.data.lessons;
                }
            }, function(response) {});
        }

        function commentOnLesson() {
            var data = {
                lessonId: vm.lesson.id,
                comment: vm.commentMsg,
                userId: vm.userInfo.id
            };
            var newComment = {
                comments: vm.commentMsg,
                timestamp: 'Just now',
                commentedBy: {
                    profilePhoto: vm.userInfo.profilePhoto,
                    fullName: vm.userInfo.fullName
                }
            };
            if ($state.is('main.comments')) {
                vm.lessonComments.push(newComment);
            } else {
                vm.lesson.comments.push(newComment);
            }
            $http.post(CommonInfo.getAppUrl() + '/api/lesson/addComment', data).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    growl.success('Comment send');
                    vm.commentMsg = "";
                }
            }, function(response) {

            });
        }

        function getCourseById(courseId) {
            $http.post(CommonInfo.getAppUrl() + '/api/course/byId', { 'courseId': courseId }).then(function(response) {
                if (response && response.data && response.data.course) {
                    CommonInfo.setInfo('course', response.data.course);
                    editCourse('edit', response.data.course);
                }
            }, function(response) {});
        }

        function editCourse(mode, course) {
            $http.get(CommonInfo.getAppUrl() + '/api/category/all').then(function(response) {
                if (response && response.data && response.data.categories) {
                    vm.categories = response.data.categories;
                }
            }, function(response) {});
            $http.post(CommonInfo.getAppUrl() + '/api/user/byType', { 'type': 'instructor' }).then(function(response) {
                if (response && response.data && response.data.users) {
                    vm.users = response.data.users;
                }
            }, function(response) {});
            vm.objMode = mode;
            if (mode == 'edit') {
                $state.go('main.editCourse');
                vm.course = course;
                vm.course.validTo = vm.course.validTo ? new Date(vm.course.validTo) : "";
            } else if (mode == 'insert') {
                vm.course = {
                    units: [],
                    instructors: []
                };
                $state.go('main.createCourse');
            }
        }

        function addUnitToCourse() {
            var values = _.compact(vm.course.courseUnit.split(','));
            _.forEach(values, function(value, key) {
                vm.course.units.push({ 'name': value });
            });
            vm.course.courseUnit = '';
        }

        function addUserToCourse() {
            var selectedInstructor = {
                fullName: vm.course.courseUser.fullName,
                userId: vm.course.courseUser.id
            };
            if (!_.find(vm.course.instructors, { 'userId': vm.course.courseUser.id }))
                vm.course.instructors.push(selectedInstructor);
            vm.course.courseUser = '';
        }

        function updateCourse(files) {
            Upload.upload({
                url: CommonInfo.getAppUrl() + '/api/course',
                data: { file: files, course: vm.course },
                method: 'POST'
            }).then(function(response) {
                response = response.data;
                if (response && !response.Error) {
                    growl.success('Course Updated successfully');
                    $state.go('main.courses');
                    getAllCourses();
                }
            }, function(resp) {
                console.log('Error status: ' + resp.status);
            });
        }

        function deleteCourse(course) {
            if (course && confirm('Are you sure you want to delete ' + course.name)) {
                var data = {
                    course: course
                };
                data.course.isDeleted = true;
                $http.post(CommonInfo.getAppUrl() + '/api/course', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Course deleted successfully');
                    }
                }, function(response) {});
            }
        }

        function getLessonById(lessonId) {
            $http.post(CommonInfo.getAppUrl() + '/api/lesson/byId', { 'lessonId': lessonId }).then(function(response) {
                if (response && response.data && response.data.lesson) {
                    CommonInfo.setInfo('lesson', response.data.lesson);
                    editLesson('edit', response.data.lesson);
                }
            }, function(response) {});
        }

        function editLesson(mode, lesson) {
            vm.objMode = mode;
            vm.lesson = lesson || { courses: [] };
            vm.lesson.newFiles = [];
            $http.get(CommonInfo.getAppUrl() + '/api/course/courseAndUnits').then(function(response) {
                if (response && response.data && response.data.courses) {
                    vm.courseUnitList = response.data.courses;
                    changeCourse();
                }
            }, function(response) {});

            if (mode == 'edit') {
                $state.go('main.editLesson');
            } else if (mode == 'insert') {
                $state.go('main.createLesson');
            }
        }

        function changeCourse() {
            if (vm.lesson && vm.lesson.courses && vm.lesson.courses.length > 0) {
                vm.lesson.unitList = _.find(vm.courseUnitList, { 'id': vm.lesson.courses[0].courseId }).units;
            }
        }

        function addFilesToLesson() {
            if (vm.lesson.file) {
                vm.lesson.newFiles = _.uniqBy(vm.lesson.newFiles.concat(vm.lesson.file), 'name');
            }
        }

        function updateLesson(files) {
            Upload.upload({
                url: CommonInfo.getAppUrl() + '/api/lesson',
                data: { file: files, lesson: vm.lesson },
                method: 'POST'
            }).then(function(response) {
                response = response.data;
                if (response && !response.Error) {
                    growl.success('Lesson Updated successfully');
                    $state.go('main.lessons');
                    getAllLessons();
                }
            }, function(resp) {
                console.log('Error status: ' + resp.status);
            });
        }

        function deleteLesson(lesson) {
            if (lesson && confirm('Are you sure you want to delete ' + lesson.name)) {
                var data = {
                    lesson: lesson
                };
                data.lesson.isDeleted = true
                $http.post(CommonInfo.getAppUrl() + '/api/lesson', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Lesson deleted successfully');
                    }
                }, function(response) {});
            }
        }

        function getLessonComments(lesson) {
            CommonInfo.setInfo('lesson', lesson);
            showLessonComments(lesson.id);
        }

        function showLessonComments(lessonId) {
            if (lessonId) {
                vm.lesson.id = lessonId
                var data = {
                    lessonId: lessonId
                }
                $http.post(CommonInfo.getAppUrl() + '/api/lesson/allComment', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        vm.lessonComments = response.data.comments;
                        if (vm.lessonComments && vm.lessonComments.length > 0)
                            $state.go('main.comments');
                        else
                            growl.info('No comments found.')
                    }
                }, function(response) {});
            }
        }

        function getUsers(type) {
            var data = {
                type: type
            };
            $http.post(CommonInfo.getAppUrl() + '/api/user/byType', data).then(function(response) {
                if (response && response.data && response.data.users) {
                    vm.users = response.data.users;
                }
            }, function(response) {});
        }

        function approveUser(user) {
            var userInfo = angular.copy(user);
            userInfo.status = 'active';
            $http.put(CommonInfo.getAppUrl() + '/api/user', userInfo).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    user.status = 'active';
                }
            }, function(response) {});
        }

        function getUserById(userId) {
            $http.post(CommonInfo.getAppUrl() + '/api/user/byId', { userId: userId }).then(function(response) {
                if (response && response.data && response.data.user) {
                    CommonInfo.setInfo('editUser', response.data.user);
                    editUser('edit', response.data.user);
                }
            }, function(response) {});
        }

        function editUser(mode, user) {
            vm.user = angular.copy(user) || {};
            vm.objMode = mode;
            if (mode == 'edit') {
                $state.go('main.editUser');
            } else if (mode == 'insert') {
                $state.go('main.createUser');
            } else if (mode == 'update') {
                $state.go('main.profile');
            }
        }

        function updateProfile(files) {
            if (vm.user) {
                Upload.upload({
                    url: CommonInfo.getAppUrl() + '/api/user',
                    data: { file: files, user: vm.user },
                    method: 'PUT'
                }).then(function(response) {
                    response = response.data;
                    if (response && !response.Error) {
                        growl.success('Profile updated successfully');
                        if (vm.objMode == 'update') {
                            vm.userInfo = response.user;
                            CommonInfo.setInfo('user', vm.userInfo);
                        }
                    }
                }, function(resp) {
                    console.log('Error status: ' + resp.status);
                });
            }
        }

        function deleteUser(user) {
            if (course && confirm('Are you sure you want to delete ' + user.name)) {
                var data = {
                    user: user
                };
                data.course.isDeleted = true;
                $http.post(CommonInfo.getAppUrl() + '/api/user', data).then(function(response) {
                    if (response && response.data && !response.data.Error) {
                        growl.success('Course deleted successfully');
                    }
                }, function(response) {});
            }
        }

        function signout() {
            CommonInfo.reset();
            $state.go('login');
        }

        function showCourseDemo(course) {
            var modalInstance = $uibModal.open({
                animation: true,
                templateUrl: 'app/partical/showDemo.html',
                size: 'lg',
                controller: function($scope, item) {
                    $scope.options = {
                        file: item.demoVideo,
                        image: item.demoPoster,
                        type: 'hls',
                        androidhls: 'true',
                        width: '100%',
                        player: 'html5',
                        aspectratio: '16:9'
                    };
                },
                resolve: {
                    item: function() {
                        return course;
                    }
                }
            });
        }

        function showCourseInfo(courseId, courseName, courseDesc) {
            if (courseId) {
                var data = {
                    courseId: courseId
                };
                $http.post(CommonInfo.getAppUrl() + '/api/unit/byCourse', data).then(function(response) {
                    if (response && response.data && response.data.units && response.data.units.length > 0) {
                        var item = {
                            courseName: courseName,
                            courseDesc: courseDesc,
                            units: response.data.units
                        };
                        var modalInstance = $uibModal.open({
                            animation: true,
                            templateUrl: 'app/partical/showCourseInfo.html',
                            controller: function($scope, item) {
                                $scope.course = item;
                            },
                            resolve: {
                                item: function() {
                                    return item;
                                }
                            }
                        });
                    } else {
                        growl.info('No information for this course yet added');
                    }
                }, function(response) {

                });
            }
        }

        function downloadFile(filePath, fileName) {
            var data = {
                fileName: fileName,
                filePath: filePath,
                userId: vm.userInfo.id
            };
            $http.post(CommonInfo.getAppUrl() + '/api/util/downloadFile', data).then(function(response) {
                console.log(response);
            }, function(response) {});
        }

        function showCategoryModal() {
            vm.categories = [];
            $http.get(CommonInfo.getAppUrl() + '/api/category/all').then(function(response) {
                if (response && response.data.categories && response.data.categories.length > 0) {
                    vm.categories = response.data.categories;
                }
            });
        }

        function addCategory() {
            console.log(vm.newCategory);
            $http.post(CommonInfo.getAppUrl() + '/api/category', vm.newCategory).then(function(response) {
                if (response && response.data && !response.data.Error) {
                    vm.categories.push(vm.newCategory);
                    vm.categoryPopup = false;
                    vm.newCategory = {};
                    growl.success('Category added successfully');
                }
            }, function(response) {});
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('ResetPasswordController', ResetPasswordController)
        .directive('loginArea', loginArea);


        /** @ngInject */
    function loginArea($window, resize) {
        return {
            restrict: 'A',
            link: function(scope, elem, attrs) {

                resize(scope).call(function() {
                    elem.css('height', $window.innerHeight + 'px');
                });
            }
        };
    }

    /** @ngInject */
    function ResetPasswordController($http, CommonInfo, $state, growl, $stateParams) {
        var vm = this;
        var token = "";

        vm.user = {
            confirmPassword: '',
            password: ''
        };

        vm.resetPassword = resetPassword;

        activate();

        function activate() {
            token = $stateParams.token;
        }

        function resetPassword() {
            if (vm.user.password && token) {
                var data = {
                    password: vm.user.password,
                    token: token
                }
                $http.post(CommonInfo.getAppUrl() + "/api/reset", data).then(
                    function(response) {
                        if (response && response.data && !response.data.Error) {
                            growl.info('Password changed successfully');
                            $state.go('login');
                        } else if (response && response.data && response.data.Error) {
                            growl.info(response.data.Message);
                        }
                    },
                    function(response) {
                        growl.info(response.data.Message);
                    }
                );
            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('LoginController', LoginController)
        .directive('loginArea', loginArea);


    //style = "background-image: url('assets/images/forumias-academy-image.jpg');background-repeat: no-repeat;background-size: cover;"
    /** @ngInject */
    function loginArea($window, resize) {
        return {
            restrict: 'A',
            link: function(scope, elem, attrs) {

                resize(scope).call(function() {
                    elem.css('height', $window.innerHeight + 'px');
                });
            }
        };
    }


    /** @ngInject */
    function LoginController($http, $state, CommonInfo, growl, $location, $timeout) {
        var vm = this;
        var codeStr = "";
        var otpTimeout;

        vm.forget = {
            status: false,
            email: ''
        };
        vm.user = {
            userName: '',
            password: ''
        };
        vm.newUser = {
            fullName: '',
            email: '',
            password: '',
            phone: '',
        };
        vm.isOtp = true;
        vm.isOtpSend = false;
        vm.isOtpValid = false;
        vm.activeForm = 1;

        vm.login = login;
        vm.signup = signup;
        vm.forgetPassword = forgetPassword;
        vm.requestOtp = requestOtp;
        vm.validateOtp = validateOtp;
        vm.isLoginPage = true;

        activate();

        function activate() {

        }

        function login() {
            if (vm.user.userName && vm.user.password) {
                $http.post(CommonInfo.getAppUrl() + "/api/login", vm.user).then(
                    function(response) {
                        if (response && response.data && response.data.result && !response.data.Error) {
                            CommonInfo.setInfo('user', response.data.result);
                            var profileType = response.data.result.profileType;
                            if (profileType == 'student')
                                $state.go('main.examsList');
                            else if (profileType == 'admin')
                                $state.go('main.courses');
                        } else if (response && response.data && response.data.Error) {
                            growl.info(response.data.Message);
                        }
                    },
                    function(response) {
                        growl.info(response.data.Message);
                    }
                );
            }
        }

        function signup() {
            if (vm.newUser.email && vm.newUser.phone && vm.newUser.fullName) {
                vm.newUser.profileType = 'student';
                $http.post(CommonInfo.getAppUrl() + "/api/user", vm.newUser).then(
                    function(response) {
                        if (response && response.data && !response.data.Error) {
                            growl.success('Signup successfuly');
                            vm.user.userName = vm.newUser.phone;
                            vm.forget = {
                                status: false
                            };
                            vm.newUser = {};
                            vm.activeForm = 0;
                        } else if (response && response.data && response.data.Code) {
                            if (response.data.isEmail) {
                                growl.info('Sorry it looks like ' + vm.newUser.email + ' belongs to an existing account. Please login using this email address');
                                vm.user.userName = vm.newUser.email;
                            } else if (response.data.isPhone) {
                                growl.info('Sorry it looks like ' + vm.newUser.phone + ' belongs to an existing account. Please login using this phone number');
                                vm.user.userName = vm.newUser.phone;
                            }
                            vm.forget = {
                                status: false
                            };
                            vm.newUser = {};
                            vm.activeForm = 0;

                        } else if (response && response.data && response.data.Error) {
                            growl.info(response.data.Message);
                        }
                    },
                    function(response) {
                        growl.info('Unable to signup, try after some time');
                    }
                );
            }
        }

        function forgetPassword() {
            if ($location.absUrl()) {
                var url = $location.absUrl();
                url = url.substring(0, url.indexOf('#') + 2) + 'reset/';
                vm.forget.host = url;
                $http.post(CommonInfo.getAppUrl() + "/api/forget", vm.forget).then(
                    function(response) {
                        if (response && response.data && !response.data.Error) {
                            growl.success('Check your email for password reset link');
                        } else if (response && response.data && response.data.Error) {
                            growl.info(response.data.Message);
                        }
                    },
                    function(response) {
                        growl.info('Mail not send, try after some time');
                    }
                );
            }
        }

        function requestOtp() {
            vm.showResendOtp = false;
            vm.newUser.otp = "";
            if (vm.newUser.phone) {
                $http.post(CommonInfo.getAppUrl() + "/api/sendOtp", vm.newUser).then(
                    function(response) {
                        if (response && response.data.secret) {
                            codeStr = response.data.secret;
                            vm.isOtpSend = true;
                            otpTimeout = $timeout(function() { vm.showResendOtp = true; }, 15000);
                        } else if (response && response.data && response.data.Code) {
                            if (response.data.isPhone) {
                                growl.info('Sorry it looks like ' + vm.newUser.phone + ' belongs to an existing account. Please login using this phone number');
                                vm.user.userName = vm.newUser.phone;
                            }
                            vm.forget = {
                                status: false
                            };
                            vm.newUser = {};
                            vm.activeForm = 0;
                        } else {
                            growl.info('Some error occured, try after some time');
                        }
                    },
                    function(response) {
                        growl.info(response.data.Message);
                    }
                );
            }
        }

        function validateOtp() {
            if (vm.newUser.otp && codeStr) {
                $timeout.cancel(otpTimeout);
                var data = {
                    code: vm.newUser.otp,
                    secret: codeStr
                }
                $http.post(CommonInfo.getAppUrl() + "/api/validateOtp", data).then(
                    function(response) {
                        if (response && !response.data.Error) {
                            vm.isOtpValid = true;
                        } else {
                            growl.info('You have entered incorrect pin');
                        }
                    },
                    function(response) {
                        growl.info(response.data.Message);
                    }
                );
            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('web')
        .factory('CommonInfo', CommonInfo)
        .factory('credentials', credentials)
        .factory('Modal', Modal)
        .directive('jwplayerjs', jwplayerjs);

    /** @ngInject */
    function jwplayerjs($compile) {
        return {
            restrict: 'EC',
            scope: {
                playerId: '@',
                setupVars: '=setup'

            },
            link: function(scope, element, attrs) {
                scope.$watch('setupVars', function(newValue, oldValue) {
                    if (scope.setupVars) {
                        var id = scope.playerId || 'random_player_' + Math.floor((Math.random() * 999999999) + 1),
                            getTemplate = function(playerId) {
                                return '<div id="' + playerId + '"></div>';
                            };

                        element.html(getTemplate(id));
                        $compile(element.contents())(scope);
                        jwplayer(id).setup(scope.setupVars);
                    }
                });
            }
        };
    }

    /** @ngInject */
    function CommonInfo($localStorage, $state) {
        return {
            getInfoObj: function() {
                return angular.copy($localStorage.infoObj);
            },
            getInfo: function(item) {
                if (!$localStorage.infoObj || !$localStorage.infoObj.user)
                    $state.go('login');
                else
                    return angular.copy($localStorage.infoObj[item]);
            },
            setInfo: function(item, value) {
                var obj = $localStorage.infoObj || {};
                obj[item] = angular.copy(value);
                $localStorage.infoObj = obj;
            },
            reset: function() {
                $localStorage.$reset();
            },
            getAppUrl: function() {
                return 'http://52.66.119.248';
            }
        };
    }

    /** @ngInject */
    function credentials(CommonInfo) {
        return {
            getCredentials: function() {
                if (CommonInfo.getInfo('user'))
                    var userType = CommonInfo.getInfo('user').profileType;
                var config = {};
                switch (userType) {
                    case 'admin':
                        config = {
                            tabs: {
                                library: false,
                                allCourses: true,
                                myCourses: false,
                                allLessons: true,
                                instructor: true,
                                students: true,
                                test: true,
                                exam: false
                            },
                            createCourse: true,
                            createLesson: true,
                            createTest: true,
                            createQuestionPaper: true,
                            createQuestion: true
                        };
                        break;
                    case 'student':
                        config = {
                            tabs: {
                                library: false,
                                allCourses: false,
                                myCourses: false,
                                allLessons: false,
                                instructor: false,
                                students: false,
                                test: false,
                                exam: true
                            },
                            createCourse: false,
                            createLesson: false,
                            createTest: false,
                            createQuestionPaper: false,
                            createQuestion: false
                        };
                        break;
                };
                return config;
            }
        };
    }

    /** @ngInject */
    function Modal($uibModal) {
        return {
            openModal: function(templateUrl) {
                var modalInstance = $uibModal.open({
                    animation: true,
                    templateUrl: templateUrl,
                    bindToController: true,
                    controllerAs: 'vm'
                });

                modalInstance.result.then(function(selectedItem) {

                }, function() {
                    $log.info('Modal dismissed at: ' + new Date());
                });
            }
        };
    }
})();

